* {
	font-family: -apple-system, system-ui, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, Helvetica Neue, Helvetica, roboto, arial;
}

.header {
	margin: 15px 15px 0 15px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.search {
	min-width: 640px;
	height: 44px;
	background-color: #eff0f2;
	border-radius: 16px;
	color: #757575;
	display: flex;
	align-items: center;
	padding: 0 15px;
}

.search svg {
	margin-right: 15px;
}

.avatar {
	width: 32px;
	height: 32px;
	border-radius: 50%;
	background-color: gray;
}

.desktop-base-header__logo-tA {
	display: inline;
	width: 28px;
	overflow: visible;
}

.desktop-base-header__logoBrand-3W {
	height: 28px;
}

.main {
	display: grid;
	grid-template-columns: 78px 1fr 376px;
	grid-template-rows: 1fr;
	gap: 15px;
	margin: 30px 15px 0 15px;
}

.sidebar {
	display: flex;
	flex-direction: column;
	gap: 15px;
}

.sidebar svg {
	width: 24px;
	height: 24px;
}

.content {}

.video-wrapper {
	border-radius: 12px;
	overflow: hidden;
}

.player {
	background-color: gray;
	aspect-ratio: 1.78;
	width: 100%;
}

.description {
	background-color: #eff0f2;
	padding: 15px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.card-channel {
	display: flex;
	align-items: center;
	gap: 15px;
}

.channel-name {
	font-size: 16px;
	font-weight: 600;
}

.counters {
	color: #757575;
	font-size: 13px;
}

.button {
	border-radius: 12px;
	background-color: #202022;
	color: #fff;
	padding: 5px 16px;
	line-height: 28px;
}

.video-info {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 15px;
}

.video-title {
	font-size: 18px;
	font-weight: 700;
	margin-bottom: 6px;
}

.video-description {
	font-size: 15px;
	color: #757575;
}

.video-controls {
	display: flex;
	align-items: center;
	gap: 5px;
	color: #757575;
}

.video-controls svg {
	fill: currentColor;
	margin-left: 10px;
}

.chat-wrapper {
	background-color: gray;
	border-radius: 12px;
	overflow: hidden;
}
